	module.exports = {"data":{"markdownRemark":{"html":"<p>In this article, you will learn how successful people think. You will learn a exercise that will allow you to transform your life. A plan you can follow that takes little time each day, one hour a day, 5 times a week.</p>\n<blockquote>\n<p>It is clear that persons who live close to their capacity, who continue to activate their potential, have a pronounced sense of well-being  and considerable energy. They see themselves as lesding purposeful and creative lives.\n-Herbert Ottto</p>\n</blockquote>\n<p>Lets look at a few facts. The average working person has athis disposal an enormous amount of free time, in fact if you total the amount of hours in a year and subtract the sleeping hours (if he sleeps 8 hours every night). You'll find he has almost (in hours)</p>\n<ul>\n<li>Waking hours = 6000</li>\n<li>Working hours = 2000</li>\n<li>Discretionary hours = 4000</li>\n</ul>\n<p>Average working man has 4000 hours a year where he is neither working nor sleeping.</p>\n<p>In those 4000 hours our mind is free.</p>\n<p>Take one hour a day five days a week and exercise your mind</p>\n<h3>Take a blank sheet of paper</h3>\n<ol>\n<li>write your primary goal at the top</li>\n<li>Right as many ideas as you can for improving that which you do now</li>\n<li>Think of 20 possible ways for improvement</li>\n</ol>\n<p><strong>Remember the following two points</strong></p>\n<ol>\n<li>it's not easy</li>\n<li>most of your ideas will not particularly be any good</li>\n</ol>\n<p>When I say it's not easy it's like forming a new habit. At first you'll find it difficult to come up with 20 ideas, and change your  familiar ways. But it is worth it</p>\n<p><strong>The most important thing the test will do will deeply embedded your goal into your mind.</strong></p>\n<p>So...</p>\n<ul>\n<li>20 ideas a day = 100 ideas a week</li>\n<li>1 hour a day X 5 days = 260 hours a year</li>\n<li>Free leisure time = 3740 hours</li>\n</ul>\n<p>You'll be thinking of your goal and how to improve your current position a full 6 1/2 working weeks a year. 6 1/2 four weeks of thinking and planning. See how easy it is to rise above the so-called competition</p>\n<p>This will still leave you 15 hours a day to do as you please</p>\n<p>Soon you will find that your mind will be working all day long. End it odd moments good ideas will be popping into your head. <em>Write them down as soon as you can</em>.</p>\n<p><strong>Just one good idea can completely revolutionize your work and your life.</strong></p>\n<p>If you want to develop the muscles of your body you need to take an exercise of some sort. The mind works in the same way.</p>\n<p><strong><em>Is it worth it.</em></strong> It's like spending five hours a week digging in the mind of gold. Each time you write your goal at the top of the sheet don't worry about it or be concerned about it. Think about it only a problem to be solved in a goal to be reached. Face it with faith. And then the powers of all your mind to solving it. And believe me solve it you will.</p>\n<h1>Recap</h1>\n<ol>\n<li>Spend one hour a day generating as many ideas as you can - to improve what you are doing now. Try for <strong>20 ideas a day.</strong></li>\n<li>Enhance your life - try to increase your mental abilities at least 20% or more (remember average person uses ~5-6%).</li>\n<li><em>Successful people are not people without problems</em> - they have <strong>learned</strong> to <strong>solve</strong> their problems</li>\n<li>Don't waste time and money worrying about needless things</li>\n<li>Use your mind effectively to achieve your goals.</li>\n</ol>\n<p>40% of them will never happen, 30% have already happened and can't be changed. 12% our needless worries about our health. 10% are petty miscellaneous worries.</p>\n<p>Only 8% are real. Try to separate The real from the unnecessary. And solve those which are in your ability to solve.</p>\n<p>Last of all the only thing I can take you to your goal is your mind</p>","frontmatter":{"title":"How Successful People Think"}}},"pathContext":{"slug":"/2017-08-08-how-successful-people-think/"}}

